.contenedorRegion {
    /* pointer-events: none; */
    width: calc(100% - 30%);
    min-height: calc(100vh - 150px);
    background-image: url(../../../img/space.gif);
    image-rendering: pixelated;
    position: relative;
    border: 0.2rem solid #fff;
    border-radius: 2rem;
    padding: 0.4em;
    box-shadow: 0 0 .2rem #fff,
              0 0 .2rem #fff,
              0 0 2rem #bc13fe,
              0 0 0.8rem #bc13fe,
              0 0 2.8rem #bc13fe,
              inset 0 0 1.3rem #bc13fe; 
}

.enemigo {
    position: absolute;
    bottom: 0;
    height: 35px;
    width: 36px;    
    background-size: 144px 35px;
    animation: reflejoEnemigo 1s steps(4) infinite;
    image-rendering: pixelated;
}

@keyframes reflejoEnemigo {
    0% {
        background-position: 0px;
    }

    100% {
        background-position: -144px;
    }
}

.imagenPinky {
    background-image: url(../../../img/sprites/pinky.png);
}
.imagenBlinky {
    background-image: url(../../../img/sprites/blinky.png);
}
.imagenClyde {
    background-image: url(../../../img/sprites/clyde.png);
}
.imagenInky {
    background-image: url(../../../img/sprites/inky.png);
}

.bala {
    position: absolute;
    background-image: url(../../../img/gif/star.gif);
    width: 40px;
    height: 40px;
    bottom: 0;
    background-size: 40px 40px;
}

.obstaculo {
    position: absolute;
    bottom: 0;
    height: 100px;
    width: 136px;    
    background-image: url(../../../img/sprites/barril.png);
    background-size: 544px 100px;
    animation: reflejoObstaculo 1s steps(4) infinite;
    image-rendering: pixelated;
}

@keyframes reflejoObstaculo {
    0% {
        background-position: 0px;
    }

    100% {
        background-position: -544px;
    }
}