.personaje {
    position: absolute;
    bottom: 0;
    height: 90px;
    width: 60px;
    background-image: url(../../../img/sprites/ship.png);
    background-size: 240px 90px;
    animation: reflejo 1s steps(4) infinite;
    image-rendering: pixelated;
}

@keyframes reflejo {
    0% {
        background-position: 0px;
    }

    100% {
        background-position: -240px;
    }
}