.footer {
    display: inline-flex;
}

.footerTexto {
    animation: glow 2s ease-in-out infinite alternate;
}

@keyframes glow {
    from {
        text-shadow: 0 0 10px rgba(255, 255, 255, 1), 0 0 20px var(--color-violeta);
    }
    to {
        text-shadow: 0 0 10px rgba(255, 255, 255, 1), 0 0 20px rgba(255, 255, 255, 1), 0 0 30px rgba(255, 255, 255, 1), 0 0 40px var(--color-violeta), 0 0 70px var(--color-violeta), 0 0 80px var(--color-violeta), 0 0 100px var(--color-violeta);
    }
}

.footerLogo {
    margin: 1em;
    width: 30px;
    height: 30px;
}