.header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    margin: 1rem;
    font-size: calc(20px + 2vmin);
    text-shadow: 0 0 10px rgba(255, 255, 255, 1), 0 0 20px var(--color-violeta);
    width: 100%;
    height: 45px;
}

.corazones {
    display: flex;
    width: 195px;
}

.corazon {
    height: 45px;
    width: 65px;
    background-image: url(../../../img/sprites/heart.png);
    background-size: 260px 45px;
    animation: reflejo 1s steps(4) infinite;
    image-rendering: pixelated;
}

@keyframes reflejo {
    0% {
        background-position: 0px;
    }

    100% {
        background-position: -260px;
    }
}