.subtitle {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .subtitle {
        animation: app-subtitle-float 2s ease-in-out infinite;
    }
}

@keyframes app-subtitle-float {
    0% {
        transform: translatey(0px);
    }

    50% {
        transform: translatey(-20px);
    }

    100% {
        transform: translatey(0px);
    }
}