@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(359deg);
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(359deg);
    }
}

.spinner:before {
    content: '';
    position: absolute;
    top: -10px;
    right: -10px;
    bottom: -10px;
    left: -10px;
    border: 3px inset #E04F97;
    border-radius: 50%;
    -webkit-animation: spin 4s infinite linear;
    animation: spin 4s infinite linear;
    box-shadow: inset 0 0 10px 2px #E04F97, 0 0 10px 2px #E04F97;
}

.spinner {
    position: absolute;
    color: #E04F97;
    font-weight: bold;
    font-size: 25px;
    top: 50%;
    left: 50%;
    width: 200px;
    height: 200px;
    margin: -100px 0 0 -100px;
    padding: 50px 0 0 50px;
    border-radius: 50%;
}

.spinner:after {
    content: '';
    position: absolute;
    top: 10px;
    right: 10px;
    bottom: 10px;
    left: 10px;
    border: 3px inset #E04F97;
    border-radius: 50%;
    -webkit-animation: spin 2s infinite linear;
    animation: spin 2s infinite linear;
    box-shadow: inset 0 0 10px 2px #E04F97, 0 0 10px 2px #E04F97;
}